import React from 'react';
import '../styles/Recommendation.css';
import Account from './Account.js';

function Recommendation() {
    return (
        <div>
            {/* Head */}
            <div className="trends__header">
                <h2>Who to follow</h2>
            </div>

            {/* Account */}
            <Account />
            <Account />

            {/* Show More */}
            <div className="trends__showMore">
                <p>Show more</p>
            </div>
        </div>
    )
}

export default Recommendation
