import React from 'react'
import '../styles/Trend.css';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';

function Trend({text}) {
    return (
        <div className="trend">
            <div className="trend__category">
                <p>Trending in Ghana</p>
                <MoreHorizIcon className="" fontSize="small" />
            </div>
            <div className="trend__topic">
                <h3>{text}</h3>
            </div>
            <div className="trend__tweets">
                <p>6,135 Tweets</p>
            </div>
        </div>
    )
}

export default Trend
